<template>
	<div class="body fundManage_body">
		<div class="table_totle_box">
			投资标的总数<span>(&nbsp;{{ total }}&nbsp;)</span>
		</div>
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%" max-height="350">
					<el-table-column prop="index" label="序号" width="100">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ (scope.$index) + (currentPage - 1) * pageSize }}
							</div>
							<div class="" v-else>
								新增
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="short_name" label="基金简称" width="150">
					</el-table-column>
					<el-table-column prop="fund_type" label="投资基金类别" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.fund_type == 1?'母基金':'自管基金' }}
							</div>
							<div class="" v-else>
								<el-select v-model="fund_type" @change="changeFundType" clearable placeholder="请选择">
								    <el-option v-for="item in fund_type_list" :key="item.value" :label="item.label" :value="item.value">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="name" label="基金全称" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.name }}
							</div>
							<div class="" v-else>
								<el-select v-model="fund_id" clearable placeholder="请选择">
								    <el-option v-for="item in fund_List" :key="item.id" :label="item.name" :value="item.id">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="is_freeze" label="是否冻结" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.is_freeze?'是':'否' }}
							</div>
							<div class="" v-else>
								<el-select v-model="is_freeze" clearable placeholder="请选择">
								    <el-option v-for="item in is_freeze_list" :key="item.value" :label="item.label" :value="item.value">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="is_master" label="基金类型" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<!-- {{ scope.row.is_master?'是':'否' }} -->
								{{scope.row.is_master_text}}
							</div>
							<div class="" v-else>
								
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</div>
							<div class="" v-else>
								
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="subscribe" label="认缴（万元）" width="120">
					</el-table-column>
					<el-table-column prop="subscribe_rate" label="认缴占比" width="120">
					</el-table-column>
					<el-table-column prop="real_fee" label="实缴（万元）" width="120">
					</el-table-column>
					<el-table-column prop="real_rate" label="实缴占比" width="120">
					</el-table-column>
					<el-table-column prop="real_production" label="实缴进度" width="120">
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" width="150">
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" width="180">
					</el-table-column>
					<el-table-column prop="service_fee" label="认购服务费" width="150">
					</el-table-column>
					<el-table-column prop="irr" label="XIRR" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<el-button type="text" size="small" @click="view(scope.row)">查看</el-button>
								<el-button type="text" size="small" @click="deleteFund(scope.row)">删除</el-button>
							</div>
							<div class="" v-else>
								<el-button type="text" size="small" @click="addSave(scope)">保存</el-button>
								<el-button type="text" size="small" @click="clearData(scope.row)">清除</el-button>
							</div>
						</template>
					</el-table-column>
				</el-table>
				<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    				</el-pagination>
			</div>
		</div>

	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getInvestorInvestGroupListApi,getInvestorInvestGroupFundSelectListApi,addInvestorInvestGroupApi,deleteInvestorInvestGroupApi } from '../../config/api'
	

	export default {
		name: 'investGroup',
		data() {
			return {
				id: '',
				total: 0,
				currentPage: 1,
				pageSize: 10,
				addData: {
					id: 0,
					unit: 3,
				},
				// 要添加的字段开始
				fund_type: '',
				fund_id: '',
				is_freeze: '',
				// 要添加的字段结束
				fund_type_list: [
					{
						value: 2,
						label: '母基金'
					},
					{
						value: 3,
						label: '自管基金'
					}
				],
				fund_List: [ ],
				is_freeze_list: [
					{
						label: '是',
						value: 1
					},
					{
						label: '否',
						value: 0
					}
				],
				tableData: [ ],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(){
				getInvestorInvestGroupListApi({
					investor: this.id
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData = this.getCurrentData(res.data.payload.data);
						this.tableData = this.tableData.map(item=>{
							item.real_production = item.real_production + "%";
							return item
						})
						this.total = res.data.payload.total;
						this.tableData.unshift(this.addData);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取分页的数据
			getCurrentData(arr){
				var currentPage = this.currentPage;
				var pageSize = this.pageSize;
				var start = (currentPage - 1) * pageSize;
				var end = currentPage * pageSize;
				var currentData = arr.slice(start,end);
				return currentData
			},
			getFundSelectList(e){
				getInvestorInvestGroupFundSelectListApi({
					type: e
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.fund_List = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addSave(){
				addInvestorInvestGroupApi({
					investor_id: this.id,
					is_freeze: this.is_freeze,
					type: this.fund_type,
					fund_id: this.fund_id
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.clearData()
						this.getInitData()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			deleteFund(e) {
				dialogConfirmHasbtn('确定要删除所选基金吗?').then(() => {
					deleteInvestorInvestGroupApi({
						id: e.id,
						type: e.fund_type
					}).then(res => {
						//console.log(res);
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							
							let length = this.tableData.length;
							if(length > 2){
								this.getInitData();
							}else{
								if(this.currentPage > 1){
									this.currentPage = this.currentPage - 1;
									this.getInitData();
								}else{
									this.getInitData();
								}
							}
							
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}).catch(() => {});
			},
			clearData(){
				this.fund_type = '';
				this.fund_List = [];
				this.fund_id = '';
				this.is_freeze = '';
			},
			//获取基金下拉列表数据
			changeFundType(e){
				this.fund_id = '';
				if (e) {
					this.getFundSelectList(e);
				} else{
					this.fund_List = [];
				}
			},
			changeFund(e){
				var item = this.getSelectedItem(this.parterCompanyList,this.fund_id);
				this.fund_type = item.fund_type;
			},
			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			// 获取选中下拉所选的值 
			getSelectedItem(data,selectedId){
				let obj = {};
				obj = data.find((item)=>{
					return item.fund_id == selectedId;
				});
				return obj
			},
			view(e){
				var type = e.fund_type;
				if (type == 1) {
					var name = 'motherFundCompanyDetail';
				} else{
					var name = 'selfAdministerFundCompanyDetail';
				}
				this.$router.push({
					name: name,
					query: { id: e.fund_id }
				})
			},
			
			toClick(data) {
				//console.log(data);
			}
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 20px 10px 30px 10px;
	}
	.table_totle_box{
		font-size: 14px;
	}
	.table_totle_box span{
		font-size: 14px;
	}
	.under_fund_header {
		margin-top: 19px;
		margin-bottom: 6px;
		color: #6292F4;
		font-size: 12px;
	}
	
	.under_fund_header span {
		color: #6292F4;
		padding: 0 4px;
	}
	/*设置权限弹出框*/
	
	.dialog_content {
		height: 340px;
		overflow: hidden;
	}
	
	.dialog_content>div {
		width: 50%;
		height: 340px;
		font-size: 14px;
		color: #545454;
	}
	
	.dialog_content_right {
		box-sizing: border-box;
		text-align: center;
		border-left: 1px solid #E5E5EA;
	}
	
	.dialog_content_title {
		margin-bottom: 40px;
		text-align: center;
	}
	
	.dialog_content_main {
		height: 310px;
		overflow-y: scroll;
	}
	
	.dialog_content_main_list {
		margin-bottom: 20px;
	}
	
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	/*设置权限弹出框结束*/
</style>


