<template>
	<div class="body fundManage_body">
		<div class="table_totle_box flex_between">
			<div class="table_totle_box_left">
				投资标的总数<span>(&nbsp;{{ totalCount }}&nbsp;)</span>
			</div>
			<div class="table_totle_box_right">
				<div class="table-group-title-btn btn-blue" @click="downloadExcel">
					<span class="iconfont icon-daochu"></span>导出excel表格
				</div>
			</div>
		</div>
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%" max-height="350">
					<el-table-column prop="index" label="序号" width="100">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{(currentPage-1)*pageSize + scope.$index }}
							</div>
							<div class="" v-else>
								新增
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="fund_type_name" label="投资标的类别" width="150">
						<template slot-scope="scope">
							
							<div class="" v-if="scope.row.id">
								{{ scope.row.fund_type_name }}
							</div>
							<div class="" v-else>
								<el-select v-model="fund_type" @change="changeParterCompany" clearable placeholder="请选择">
								    <el-option v-for="item in fund_type_list" :key="item.fund_type" :label="item.fund_type_name" :value="item.fund_type" >
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="name" label="投资标的全称" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.name }}
							</div>
							<div class="" v-else>
								<el-select v-model="fund_id" filterable clearable placeholder="请选择">
								    <el-option v-for="item in parterCompanyList" :key="item.fund_id" :label="item.name" :value="item.fund_id">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="subscribe" label="认缴（万元）" width="180">
						<!-- <template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.subscribe }}
							</div>
							<div class="" v-else>
								<el-input v-model="subscribe" placeholder="请输入内容" clearable>
								</el-input>
							</div>
						</template> -->
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							<div v-if="scope.row.unit == 1">
								人民币
							</div>
							<div v-else-if="scope.row.unit == 2">
								美元
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="multiple" label="倍数" width="180">
					</el-table-column>
					<el-table-column prop="investment" label="投资（万元）" width="180">
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" width="150">
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" width="180">
					</el-table-column>
					<el-table-column prop="irr" label="XIRR" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<el-button type="text" size="small" @click="view(scope.row)">查看</el-button>
								<el-button type="text" size="small" @click="deleteCompanyRunInvestGroup(scope.row)">删除</el-button>
							</div>
							<div class="" v-else>
								<el-button type="text" size="small" @click="save(scope)">保存</el-button>
								<el-button type="text" size="small" @click="clearData">清除</el-button>
							</div>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span> 
						</div>
					</template>
				</el-table>
				<el-pagination v-if="totalCount" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="totalCount">
    			</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getCompanyRunInvestListApi,getCompanyRunParterCompanyListApi,addCompanyRunInvestGroupApi,deleteCompanyRunInvestGroupApi,downloadExcelCompanyRunInvestGroupApi } from '../../config/api'
	
	export default {
		name: 'companyRunInvestGroup',
		inject: ['reload'],
		data() {
			return {
				id: '',
				currentPage: 1,
				pageSize: 10,
				totalCount: 0,
				parterCompanyList: [],
				addData: {
					id: 0,
					unit: 3,
				},
				fund_type_list: [],
				// 要添加的字段开始
				fund_id: '',
				fund_type: '',
				subscribe: 0,
				// 要添加的字段结束
				tableData: [],
				auth: '',
				no_power_text: '您暂无权限进行此操作！',
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getCompanyRunParterCompanyList()
			this.getCompanyRunInvestList()
		},
		mounted() {},
		components: {},
		methods: {
			getCompanyRunInvestList(){
				getCompanyRunInvestListApi({
					sg_company_manager_id: this.id,
					page: this.currentPage,
					size: this.pageSize
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.totalCount = res.data.payload.count;
						this.auth = res.data.payload.auth;
						this.tableData.unshift(this.addData);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getCompanyRunParterCompanyList(){
				getCompanyRunParterCompanyListApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.all_parterCompanyList = res.data.payload.data;
						this.fund_type_list = res.data.payload.fund_types;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			save(){
				if (this.auth.insert_operate) {
					addCompanyRunInvestGroupApi({
						sg_company_manager_id: this.id,
						fund_id: this.fund_id,
						// subscribe: this.subscribe,
						subscribe:0,
						fund_type: this.fund_type
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.fund_id = '';
							// this.subscribe = '';
							this.fund_type = '';
							dialogConfirm("success", res.data.message);
							this.getCompanyRunParterCompanyList()
							this.getCompanyRunInvestList()
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else {
					dialogConfirm("info", this.no_power_text);
				}
			},
			deleteCompanyRunInvestGroup(row){
				if (this.auth.delete_operate) {
					var fund_id = row.id;
					dialogConfirmHasbtn('确定要删除所选基金吗?').then(() => {
						deleteCompanyRunInvestGroupApi({
							sg_company_manager_id: this.id,
							id: fund_id,
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								
								let length = this.tableData.length;
								if(length > 1){
									this.getCompanyRunInvestList();
								}else{
									if(this.currentPage > 1){
										this.currentPage = this.currentPage - 1;
										this.getCompanyRunInvestList();
									}else{
										this.getCompanyRunInvestList(this.currentPage);
									}
								}
								
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}).catch(() => {});
				} else{
					dialogConfirm("info", this.no_power_text);
				}
			},
			changeParterCompany(e){
				this.fund_id = '';
				if (e) {
					this.parterCompanyList = this.all_parterCompanyList[e];
				}
			},
			view(e){
				if (this.auth.check_operate) {
					var type = e.fund_type;
					var component = '';
					if (type == 1) {
						component = "motherFundCompanyDetail";
					} else if(type == 2){
						component = "selfAdministerFundCompanyDetail";
					} else {
						component = "subFundDetail";
					}
					this.$router.push({
						name: component,
						query: {
							id: e.fund_id
						}
					})
				} else{
					dialogConfirm("info", this.no_power_text);
				}
			},
			downloadExcel(){
				downloadExcelCompanyRunInvestGroupApi({
					sg_company_manager_id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			clearData(){
				this.fund_id = '';
				this.fund_type = '';
				this.subscribe = '';
			},
			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getCompanyRunInvestList()
			},
			changeCurrentPage(e){
				this.currentPage = e;
				this.getCompanyRunInvestList()
			},
			// 获取选中下拉所选的值 
			getSelectedItem(data,selectedId){
				let obj = {};
				obj = data.find((item)=>{
					return item.fund_id == selectedId;
				});
				return obj
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 0px 30px 0px;
	}
	/*公司名称模块开始*/
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	/*公司名称模块结束*/
	
	.under_fund_header {
		margin-top: 19px;
		margin-bottom: 6px;
		color: #6292F4;
		font-size: 12px;
	}
	
	.under_fund_header span {
		color: #6292F4;
		padding: 0 4px;
	}
	/*设置权限弹出框*/
	
	.dialog_content {
		height: 340px;
		overflow: hidden;
	}
	
	.dialog_content>div {
		width: 50%;
		height: 340px;
		font-size: 14px;
		color: #545454;
	}
	
	.dialog_content_right {
		box-sizing: border-box;
		text-align: center;
		border-left: 1px solid #E5E5EA;
	}
	
	.dialog_content_title {
		margin-bottom: 40px;
		text-align: center;
	}
	
	.dialog_content_main {
		height: 310px;
		overflow-y: scroll;
	}
	
	.dialog_content_main_list {
		margin-bottom: 20px;
	}
	
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	/*设置权限弹出框结束*/
</style>


